<template>
  <pull-to :top-load-method="onRefresh" :top-config="pullRefreshConfig">
    <div class="home_wrap">
      <img src="@/assets/banner.jpg" alt="" class="home_wrap_banner">
      <!--  -->
      <div v-if="showList" style="padding-bottom: 1.7rem">
        <div class="home_wrap_item" v-for="item in homeList" :key="item.id">
          <router-link :to="{path: '/details/', query: { id: item.id }}">
            <div class="time">{{item.createDatePage}}<span :class="item.statusClass">{{item.statusPage}}</span>
            </div>
            <div class="content">
              <img :src="item.category.icon" alt="" class="pic">
              <div>
                <div class="name">{{item.category.name}}</div>
                <div class="price"><span>{{item.price}}</span></div>
              </div>
            </div>
          </router-link>
        </div>
      </div>
      <!--  -->
      <div v-else>
        <img src="@/assets/recycling.png" alt="" class="home_wrap_icon">
        <div class="home_wrap_ordername"></div>
        <div class="home_wrap_btnname"></div>
      </div>
      <!-- footer -->
      <div class="home_wrap_footer">
        <img src="@/assets/icon_footer.png" alt="" class="home_wrap_footer_tbg">
        <router-link to="/classIfy">
          <div class="footer_circle"><br/></div>
        </router-link>
        <router-link to="/records">
          <div class="footer_left"><img src="@/assets/icon_foot.png" alt="" class="icon"><span></span></div>
        </router-link>
        <router-link to="/orders">
          <div class="footer_right"><img src="@/assets/icon_record.png" alt="" class="icon"><span></span></div>
        </router-link>
      </div>
    </div>
  </pull-to>
</template>
<script>
  import '@/assets/detailstyle/home.css'
  import api from '@/api/api.js'
  import {mapGetters} from 'vuex';
  import PullTo from 'vue-pull-to'

  export default {
    name: "home",
    components: {
      PullTo
    },
    data() {
      return {
        homeList: {},
        showList: true,
        pullRefreshConfig: {
          pullText: '', // 
          triggerText: '', // 
          loadingText: '...', // 
          doneText: '', // 
          failText: '', // 
          loadedStayTime: 800, // ms
          stayDistance: 80, // 
          triggerDistance: 100 // 
        }
      }
    },
    created() {
      var urlToken = location.search;
      var tk = urlToken.split("=");
      // var testToken = ""
      // this.$store.dispatch('getToken', testToken)
      this.$store.dispatch('getToken', tk[1])
    },
    computed: mapGetters({
      token: "token"
    }),
    mounted() {
      //token
      var token = this.$route.query.token;
      window.localStorage.setItem('token', token);
      this.getData();
    },
    methods: {
      onRefresh(loaded) {
        //
        this.getData(loaded)
      },
      getData(loaded) {
        //
        api.getHome({
          "app_key": "app_id_1",
          token: this.token

        }).then((res) => {
          console.log(res.data);
          if (res.data.length === 0) {
            this.showList = false;
          } else {
            res.data.map(items => {
              var status = items.status4Page;
              switch (status) {
                case 'distribute':
                  items.statusClass = 'complete';
                  break;
                case 'ALREADY':
                  items.statusClass = 'complete';
                  break;
                case 'INIT':
                  items.statusClass = 'waiting';
                  break;
                default:
                  break;
              }
            });
            this.homeList = res.data;
            if (loaded) {
              loaded('done')
            }
          }
        }).catch((error) => {
          console.log(error)
        })
      }
    },
  }
</script>
<style>
  .van-pull-refresh {
    width: 100%;
  }

  .vue-pull-to-wrapper {
    width: 100%;
  }

  .van-toast {
    font-size: 0.3rem;
    line-height: 0.012rem;
    border-radius: 0.1rem;
  }

  .van-toast--text {
    padding: 0.6rem;
    min-width: 1.6rem;
  }

  .van-pull-refresh__head {
    height: 0.5rem;
    top: -0.5rem;
    font-size: 0.24rem;
    line-height: 0.5rem;
  }
</style>





