<template>
    <div class="addressopt">
        <!-- <header class="head clearfix">
            <div @click="backbtn()" class="fl">
                <span class="fl headback"></span>
                <span class="fl"></span>
            </div>
            <h3 class="fl"></h3>
        </header> -->
        <div class="step_call">
            <h3></h3>
            <ul class="step_list">
                <li class="item">
                    <div class="step_num">1</div>
                    <span></span>
                    <div class="xuxian"></div>
                </li>
                <li class="item">
                    <div class="step_num">2</div>
                    <span></span>
                    <div class="xuxian"></div>
                </li>
                <li class="item">
                    <div class="step_num">3</div>
                    <span></span>
                    <div class="xuxian"></div>
                </li>
                <li class="item">
                    <div class="step_num">4</div>
                    <span></span>
                    <!-- <div class="xuxian"></div> -->
                </li>
            </ul>
            <div class="icon_smaell"></div>
            <h5 class="reward">1</h5>
        </div>
        <!--  -->
        <div class="hsdiqu clearfix"   >
            <h5 class="fl"></h5>
            <router-link class="text fl" to="/addressel/areaelecte"  >
                {{userAddressInfo.address}}
            </router-link>
            <span class="fr"></span>

        </div>
        <div class="next">
            <span class="add_info">?</span>
            <h1 id="demo"></h1>
            <div  class="nextbutton  ">
                <a  v-show="!isEnter"   class="dontEnter"     href="javascript:;"></a>
                <router-link v-show="isEnter"   class="yesEnter"  :to="'/typeSelect/'+addRessId.id" ></router-link>
            </div>


        </div>
        <!--  -->
        <!-- <prompt-box></prompt-box> -->

    </div>
</template>
<script>
import '@/assets/createstyle/tool.css'
import '@/assets/createstyle/addressopt.css'
import api from '@/api/api.js'
import PromptBox from './common/promptbox.vue'

import {mapGetters} from 'vuex'
export default {
  components:{
      PromptBox
  },
    data(){
        return{
            itemID:'',
            userAddressInfo:{
                areaId:'',
                address:''
            },
            areaList:'',
            id:'',
            isEnter:false
        }
    },
    computed: mapGetters({
        useraddress : 'useraddress',
        addRessId   : 'addRessId',
        token       : 'token'
    }),
  created(){
    this.itemID = this.$store.state.msItemId;
    var {id} = this.$route.params;   //ID
    this.id = id;
    api.getAddRessList({
        "app_key": "app_id_1",
        "data": {
            "areaId": 2,
            "id":id,
            "pageBean": {
            "pageNumber": 1,
            "pageSize": 20
            }
        },
        token:this.token
    }).then((res)=>{
        // 
         if(this.useraddress.address){
            this.isEnter = true;
            this.userAddressInfo.address = this.useraddress.address + this.useraddress.name;
             
         }else{
             if(res.data.isExist){
                this.isEnter = true;
                this.userAddressInfo.address  = res.data.address + res.data.name
                this.userAddressInfo.areaId   = res.data.areaId
                this.userAddressInfo.id   = res.data.id
                this.$store.dispatch('changeAddressText',this.userAddressInfo)
             }else{
                 this.isEnter = false;
                 this.userAddressInfo.address = ''
             }
         }
    }).catch((erro)=>{
        console.log(erro)
    })
  },
  methods:{
    backbtn(){ //
        this.$router.go(-1);
    }
}
}
</script>



