<template>
  <imgView v-if="showImgView" :picUrl="picUrl" @toggleImgView="toggleImgView"></imgView>
  <div class="details_wrap" v-else>
    <div class="details_wrap_item">
      <div class="time">{{detailsList.orderNo}}<span
        :class="detailsList.statusClass">{{detailsList.statusPage}}</span></div>
      <div class="date">{{detailsList.createDatePage}}</div>
      <div class="content">
        <img :src="detailsList.category?detailsList.category.icon:''" alt="" class="pic">
        <div>
          <div class="name">{{detailsList.category ? detailsList.category.name : ''}}</div>
          <div class="price"><span><span class="money">{{detailsList.price}}</span></span></div>
        </div>
      </div>
    </div>
    <div class="details_wrap_time"><span v-show="detailsList.level=='0'">{{detailsList
      .arrivalTimePage}}</span><span class="btn_cancel" @click="openOrders"
                                     v-show="detailsList.status4Page!=='COMPLETE'&&detailsList.status4Page!=='CANCEL'&&detailsList.status4Page!=='REJECTED'"></span>
    </div>
    <!-- div -->
    <!--  -->
    <div class="details_wrap_reason" v-show="detailsList.status4Page=='CANCEL'||detailsList.status4Page=='REJECTED'">
      <div class="why"></div>
      <div class="answer">{{detailsList.cancelReason}}</div>
    </div>
    <!--  -->
    <div class="details_wrap_reason" v-show="detailsList.status4Page=='COMPLETE'||detailsList.status4Page=='ALREADY'">
      <div class="why">{{detailsList.recyclers?detailsList.recyclers.name:''}}<span class="btn_view"
                                                                                    @click="openEvaluation"
                                                                                    v-show="detailsList.status4Page=='COMPLETE'">{{detailsList.isEvaluated == '1' ? '' : ''}}</span>
      </div>
      <a :href="tel" class="tel"><img src="@/assets/icon_tel.png" alt=""
                                      class="icon_tel">{{detailsList.recyclers?detailsList.recyclers
        .tel:''}}</a>
    </div>
    <!--   -->
    <div class="details_wrap_reason" v-show="detailsList.status4Page=='distribute'">
      <div class="why"></div>
      <div class="answer">1-3</div>
    </div>
    <div class="details_wrap_info">
      <div class="title"></div>
      <div class="picture">
        <img :src="pic.picUrl" alt="" v-for="pic in detailsPic" :key="pic.id" @click="toggleImgView(true)">
      </div>
      <div class="description">{{detailsList.remarks}}</div>
      <div class="lable">
        <span v-for="des in detailsDes" :key="des.id">{{des.categoryAttrOpptionName}}</span>
      </div>
    </div>
    <div class="details_wrap_belongs">
      <div class="text">{{detailsList.company?detailsList.company.name:''}}</div>
      <div class="text">{{detailsList.company?detailsList.company.tel:''}}</div>
    </div>
    <!--  -->
    <div class="details_wrap_footbtn" @click="openCode"
         v-show="detailsList.status4Page=='ALREADY'||detailsList.status4Page=='distribute'">
    </div>
    <div class="details_shadow" v-if="showShadow"></div>
    <!--  -->
    <div class="details_shadow_box" v-if="showOrders">
      <div class="title"></div>
      <div class="button_holder">
        <input type="radio" id="radio-1" name="radio" class="regular_radio" value="" v-model="cancelReason"/><label
        for="radio-1"><i></i></label>
        <input type="radio" id="radio-2" name="radio" class="regular_radio" value=""
               v-model="cancelReason"/><label for="radio-2"><i></i></label>
        <input type="radio" id="radio-3" name="radio" class="regular_radio" value="" v-model="cancelReason"/><label
        for="radio-3"><i></i></label>
        <input type="radio" id="radio-4" name="radio" class="regular_radio" value="" v-model="cancelReason"/><label
        for="radio-4"><i></i></label>
        <input type="radio" id="radio-5" name="radio" class="regular_radio" value="" v-model="cancelReason"/><label
        for="radio-5"><i></i></label>
      </div>
      <div class="button_footer">
        <div class="btn_cancel" @click="closeOrders"></div>
        <div class="btn_confirm" @click="submitCancelOrders"></div>
      </div>
    </div>
    <!--  -->
    <div class="details_cancelSucceed_box" v-if="showCancel"></div>
    <!--  -->
    <div class="details_shadow_code" v-if="showCode">
      <img src="@/assets/icon_delete.png" alt="" class="icon_delete" @click="closeCode">
      <div class="code_text"></div>
      <qrCode :url="url"></qrCode>
    </div>
    <!--  -->
    <div class="details_shadow_evaluation" v-if="showEvaluation">
      <div class="title" v-show="detailsList.isEvaluated == '0'"></div>
      <div class="title" v-show="detailsList.isEvaluated == '1'"></div>
      <div class="evaStar">
        <ul class="star">
          <li v-for="(itemClass,index) in itemClasses" :key="itemClass.id" :class="itemClass" class="star-item"
              @click="stars(index)"
              track-by="index"></li>
        </ul>
      </div>
      <textarea name="" id="" class="text" v-model="evaluateText" :disabled="detailsList.isEvaluated=='1'"
                placeholder=""></textarea>
      <div class="footer_btn" @click="closeEvaluation" v-show="detailsList.isEvaluated == '1'"></div>
      <div class="footer_btn" @click="setEvaluate" v-show="detailsList.isEvaluated == '0'"></div>
    </div>
  </div>
</template>

<script>
  import '@/assets/detailstyle/details.css'
  import api from '@/api/api.js'
  import imgView from '@/components/details/imgView.vue'
  import qrCode from '@/components/details/qrCode.vue'

  export default {
    data() {
      return {
        showShadow: false,
        showOrders: false,
        showEvaluation: false,
        showCode: false,
        showCancel: false,
        score: 5,
        detailsList: {},
        detailsPic: {},
        detailsDes: {},
        id: this.$route.query.id,
        cancelReason: '',
        evaluateText: '',
        showImgView: false,
        picUrl: [],
        url: '',
        tel: ''
      }
    },
    mounted() {
      //
      this.getData();
      //
      this.getEvaluate();
    },
    components: {
      imgView, qrCode
    },
    computed: {
      //
      itemClasses() {
        var result = []; // ,
        var score = Math.floor(this.score * 2) / 2; // 
        var hasDecimal = score % 1 !== 0; // 
        var integer = Math.floor(score); // 
        for (var i = 0; i < integer; i++) { // on
          result.push("on"); // pushCLS_ON
        }
        if (hasDecimal) { // half
          result.push("half"); // 
        }
        while (result.length < 5) {// ,off
          result.push("off");
        }
        return result;
      }
    },
    methods: {
      getData() {
        api.getDetails({
          "app_key": "app_id_1",
          "data": {
            "id": this.id,
            "isEvaluated": "0",
            "status": 0
          },
          token: this.$store.state.token
        }).then((res) => {
          console.log(res);
          var status = res.data.order.status4Page;
          this.picUrl = res.data.orderPicList;
          switch (status) {
            //
            case 'distribute':
              res.data.order.statusClass = 'complete';
              break;
            //
            case 'ALREADY':
              res.data.order.statusClass = 'complete';
              break;
            //
            case 'INIT':
              res.data.order.statusClass = 'waiting';
              break;
            //
            case 'CANCEL':
              res.data.order.statusClass = 'cancel';
              break;
            //
            case 'REJECTED':
              res.data.order.statusClass = 'cancel';
              break;
            //
            case 'COMPLETE':
              res.data.order.statusClass = 'succeed';
              break;
            default:
              break;
          }
          this.detailsList = res.data.order;
          this.tel = "tel:" + res.data.order.recyclers.tel;
          this.url = res.data.order.orderNo;
          this.detailsPic = res.data.orderPicList;
          this.detailsDes = res.data.OrderItemList;
        }).catch((error) => {
          console.log(error)
        })
      },
      openOrders() {
        this.showShadow = true;
        this.showOrders = true;
        document.querySelector('.details_wrap').style.overflow = 'hidden';
      },
      closeOrders() {
        this.showShadow = false;
        this.showOrders = false;
        document.querySelector('.details_wrap').style.overflow = 'auto';
      },
      openEvaluation() {
        this.showShadow = true;
        this.showEvaluation = true;
        document.querySelector('.details_wrap').style.overflow = 'hidden';
      },
      closeEvaluation() {
        if ('detailsList.isEvaluated=="1"') {
          this.showShadow = false;
          this.showEvaluation = false;
          document.querySelector('.details_wrap').style.overflow = 'auto';
          return
        }
        this.setEvaluate();
        this.showShadow = false;
        this.showEvaluation = false;
        document.querySelector('.details_wrap').style.overflow = 'auto';
      },
      openCode() {
        this.showShadow = true;
        this.showCode = true;
        document.querySelector('.details_wrap').style.overflow = 'hidden';
      },
      closeCode() {
        this.showShadow = false;
        this.showCode = false;
        document.querySelector('.details_wrap').style.overflow = 'auto';
      },
      stars(index) {
        if (this.detailsList.isEvaluated === '0') {
          this.score = index + 1
        }
      },
      timedMsg() {
        setTimeout(() => {
          this.showCancel = false;
          this.showShadow = false;
          document.querySelector('.details_wrap').style.overflow = 'auto';
          this.$router.push({
            path: '/orders'
          })
        }, 1000);
      },
      submitCancelOrders() {
        api.cancelOrders({
          "app_key": "app_id_1",
          "data": {
            "id": this.id,
            "cancelReason": this.cancelReason,
            "isEvaluated": "0"
          },
          token: this.$store.state.token
        }).then((res) => {
          console.log(res.data);
          this.showOrders = false;
          this.showCancel = true;
          this.timedMsg();
        }).catch((error) => {
          console.log(error)
        })
      },
      getEvaluate() {
        api.getEvaluate({
          "app_key": "app_id_1",
          "data": {
            "id": this.id,
          },
          token: this.$store.state.token
        }).then((res) => {
          console.log(res);
          if (res.data) {
            this.score = res.data.score;
            this.evaluateText = res.data.content;
          } else {
          }
        }).catch((error) => {
          console.log(error)
        })
      },
      setEvaluate() {
        api.setEvaluate({
          "app_key": "app_id_1",
          "data": {
            "recyclerId": 0,
            "orderId": this.id,
            "score": this.score,
            "content": this.evaluateText,
            "memberId": 0
          },
          token: this.$store.state.token
        }).then((res) => {
          this.$router.push({
            path: '/orders'
          })
          // if (res.status=="success") {
          //   this.$router.push({
          //     path: '/orders'
          //   })
          //   this.getData();
          // }
        }).catch((error) => {
          console.log(error)
        })
      },
      toggleImgView(v) {
        this.showImgView = v
      }
    }
  }
</script>



