<template>
    <div class="uploadimage">
        <!-- <header class="head clearfix">
            <div @click="backbtn()" class="fl">
                <span class="fl headback"></span>
                <span class="fl"></span>
            </div>
            <h3 class="fl"></h3>
        </header> -->
        <div class="imgbtnortext">
            <!--  -->
            <div v-show="imgsAddress.length<1"  class="upimg">
                <div class="bcphotoimg" style=" width:100% ; height:100%;"> 
                    <div class="show"> 
                        <div class="picture" :style="'backgroundImage:url('+headerImage+')'"></div> 
                    </div> 
                    <div style="width:100%;height:100%"> 
                        <input style="width:100%;height:100%;  opacity:0;" type="file" id="upload"  accept="image" @change="upload"> 
                        <label for="upload"></label> 

                        <!-- <van-uploader style="width:100%;height:100%;"       :after-read="upload"     >
                          <van-icon      name="photograph" />
                        </van-uploader> -->
                    </div> 
                </div> 
            </div>
            <!--  -->
            <div class="showimge">
                <div  v-for="(item,index) in imgsAddress"  :key="item.id"    class="imgBox"   >
                  <img  :src="imgsAddress[index].thumbnail"   alt="">
                  <div class="zhezhao"></div>
                  <van-icon  @click="removeImg(index)"  class="closeImg" name="clear" />
                </div>
                <!-- <img src="@/assets/upimg2.png" alt=""> -->
                <div v-show="imgsAddress.length>0 && imgsAddress.length<3" class="isaddimg">
                    <span class="level"></span>
                    <span class="verticals"></span>
                    <div style=" width:100% ; height:100%;"> 
                      <div class="show"> 
                          <div class="picture" ></div> 
                      </div> 
                      <div style="width:100%;height:100%"> 
                         <input style="width:100%;height:100%;  opacity:0;" type="file" id="upload"  accept="image" @change="upload"> 
                         <label for="upload"></label> 
                        <!-- <van-uploader :after-read="upload">
                          <van-icon name="photograph" />
                        </van-uploader> -->
                      </div> 
                    </div> 
                </div>
            </div>
            <div   class="text"></div>
        </div>
        <div class="recycle_description">
            <h4></h4>
            <div class="textarea_box">
                <textarea rows="3" maxlength="150"  v-model="textareaValue" placeholder="" cols="20"  >
                
                </textarea>
                <span>{{textareaValue.length}}/150</span>
            </div>
            <div class="nextbutton "  @click="getTextareaValue"  >

                <a  v-if="imgsAddress.length<=0"    style="background-color:#bfc5c8;"   href="javascript:;"></a>
              
                <router-link v-if="imgsAddress.length>0"   to="/estimate" ></router-link>
            </div>
        </div>

        
    </div>

</template>

<style>
.img {
  display: block;
  max-width: 100%;
  margin: 20px auto;
}
</style>
<script>
import Exif from "exif-js";
import api from "@/api/api.js";
import $ from "jquery";
import axios from "axios";

import "@/assets/createstyle/uploadimage.css";
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      headerImage: "",
      textareaValue: "", //
      number: 0
    };
  },
  computed: mapGetters({
    imgsAddress: "imgsAddress",
    token       : 'token'
  }),
  methods: {
    backbtn() {
      //
      this.$router.go(-1);
    },
    removeImg(index) {
      this.$store.dispatch("removeImg", index);
    },
    getTextareaValue() {
      this.$store.dispatch("changeTextareaValue", this.textareaValue); //
    },
    upload(e) {
      let files = e.target.files || e.dataTransfer.files; 
      if (!files.length) return; 
      this.picValue = files[0]; 
      this.imgPreview(this.picValue); 
    },
    imgPreview(file) {
      let self = this;
      let Orientation;
      //
      Exif.getData(file, function() {
        Orientation = Exif.getTag(this, "Orientation");
      });
      // FileReader
      if (!file || !window.FileReader) return;
      if (/^image/.test(file.type)) {
        // reader
        let reader = new FileReader();
        // 2 base64 
        reader.readAsDataURL(file);
        // 
        reader.onloadend = async function() {
          let result = this.result;
          let img = new Image();
          img.src = result;
          //100K,
          if (this.result.length <= 100 * 1024) {
            self.headerImage = this.result;
            self.postImg();
          } else {
            img.onload = function() {
              let data = self.compress(img, Orientation);
              self.headerImage = data;
              self.postImg();
            };
          }
        };
      }
    },
    postImg() {
      //

      ap.showLoading({
        content: '',
      });
      api.upLoader({
        "app_key": "app_id_1",
          "data": {
                "fileContentBase64": this.headerImage,
                "fileName": ''
              },
            token:this.token
      }).then((res)=>{
        this.$store.dispatch("addImgsUrl", res.data[0]);
        ap.hideLoading()
      }).catch((err) =>{
        console.log(err)
      })

    },
    rotateImg(img, direction, canvas) {
      //4
      const min_step = 0;
      const max_step = 3;
      if (img == null) return;
      //imgimg
      let height = img.height;
      let width = img.width;
      let step = 2;
      if (step == null) {
        step = min_step;
      }
      if (direction == "right") {
        step++;
        //
        step > max_step && (step = min_step);
      } else {
        step--;
        step < min_step && (step = max_step);
      }
      //
      let degree = step * 90 * Math.PI / 180;
      let ctx = canvas.getContext("2d");
      switch (step) {
        case 0:
          canvas.width = width;
          canvas.height = height;
          ctx.drawImage(img, 0, 0);
          break;
        case 1:
          canvas.width = height;
          canvas.height = width;
          ctx.rotate(degree);
          ctx.drawImage(img, 0, -height);
          break;
        case 2:
          canvas.width = width;
          canvas.height = height;
          ctx.rotate(degree);
          ctx.drawImage(img, -width, -height);
          break;
        case 3:
          canvas.width = height;
          canvas.height = width;
          ctx.rotate(degree);
          ctx.drawImage(img, -width, 0);
          break;
      }
    },
    compress(img, Orientation) {
      let canvas = document.createElement("canvas");
      let ctx = canvas.getContext("2d");
      //canvas
      let tCanvas = document.createElement("canvas");
      let tctx = tCanvas.getContext("2d");
      let initSize = img.src.length;
      let width = img.width;
      let height = img.height;
      //400
      let ratio;
      if ((ratio = width * height / 4000000) > 1) {
        console.log("400");
        ratio = Math.sqrt(ratio);
        width /= ratio;
        height /= ratio;
      } else {
        ratio = 1;
      }
      canvas.width = width;
      canvas.height = height;
      // 
      ctx.fillStyle = "#fff";
      ctx.fillRect(0, 0, canvas.width, canvas.height);
      //100
      let count;
      if ((count = width * height / 1000000) > 1) {
        console.log("100W");
        count = ~~(Math.sqrt(count) + 1); //
        //  
        let nw = ~~(width / count);
        let nh = ~~(height / count);
        tCanvas.width = nw;
        tCanvas.height = nh;
        for (let i = 0; i < count; i++) {
          for (let j = 0; j < count; j++) {
            tctx.drawImage(
              img,
              i * nw * ratio,
              j * nh * ratio,
              nw * ratio,
              nh * ratio,
              0,
              0,
              nw,
              nh
            );
            ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh);
          }
        }
      } else {
        ctx.drawImage(img, 0, 0, width, height);
      }
      //ios 
      if (Orientation != "" && Orientation != 1) {
        switch (Orientation) {
          case 6: //90
            this.rotateImg(img, "left", canvas);
            break;
          case 8: //90
            this.rotateImg(img, "right", canvas);
            break;
          case 3: //180
            this.rotateImg(img, "right", canvas); //
            this.rotateImg(img, "right", canvas);
            break;
        }
      }
      //
      let ndata = canvas.toDataURL("image/jpeg", 0.1);
      console.log("" + initSize);
      console.log("" + ndata.length);
      console.log(
        "" + ~~(100 * (initSize - ndata.length) / initSize) + "%"
      );
      tCanvas.width = tCanvas.height = canvas.width = canvas.height = 0;
      return ndata;
    }
  }
};
</script>
<style>
* {
  margin: 0;
  padding: 0;
}
.show {
  width: 100px;
  height: 100px;
  overflow: hidden;
  position: relative;
  border-radius: 50%;
  border: 1px solid #d5d5d5;
}
.picture {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.yd-loading {
  transform: scale(2);
}
</style>





